.advantages__intro {
  color: #283645;
  text-align: center;
  font-size: 24px;
  font-style: normal;
  font-weight: 300;
  line-height: 28.8px;
}
@media (min-width: 660px) {
  .advantages__intro {
    font-size: 40px;
    line-height: 54.47px;
  }
}
@media (min-width: 1080px) {
  .advantages__intro, br {
    font-size: 50px;
    line-height: 68.09px;
  }
  br {
    display: none;
  }
}